import { Component, OnInit } from '@angular/core';
import { NavController, PopoverController, LoadingController, ActionSheetController, AlertController } from '@ionic/angular';
import {  AppMin } from "../core";
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-complaint',
  templateUrl: './complaint.page.html',
  styleUrls: ['./complaint.page.scss'],
})
export class ComplaintPage implements OnInit {
  //举报咨询师页面
  loader;
  orderId;
  cause = ''; //投诉原因
  OtherCause = '';//其他原因
  toUserCName;
  txt;
  date;
  isjubao = 'True';
  header_class;
  constructor(public navCtrl: NavController, public ActivatedRoute: ActivatedRoute, private alertCtrl: AlertController, public loadingCtrl: LoadingController, public appMin: AppMin) {

    this.orderId = this.ActivatedRoute.snapshot.queryParams['orderId'];
    this.toUserCName = this.ActivatedRoute.snapshot.queryParams['toUserCName'];


    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }

  

  }

  send(value, isshow?: boolean) {
    // if (isshow) {
    //   this.loader = this.loadingCtrl.create({
    //     message: "加载中...",
    //     duration: 3000
    //   });
    //   this.loader.present();
    // }



    this.appMin.get(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


    // try {
    //   this.appMin.send(value);
    //   console.log("发送了数据=" + value);
    // } catch (error) {
    //   console.log("发送失败" + error.message);

    // }


  }

  addComplaintData() {
    //加载初始数据
    console.log('addComplaintData');
    let obj = {
      type: 'addComplaintData',
      orderId: this.orderId
    }
    this.send(JSON.stringify(obj));
  }
  async  submitts() {
    //投诉按钮
    console.log('cause=' + this.cause);
    console.log('OtherCause=' + this.OtherCause);

    if (this.cause != "") {

    } else if (this.OtherCause != "") {

    } else {
      let alert = await this.alertCtrl.create({
        header: '',
        subHeader: '请至少填写一项。',
        buttons: ['确定']
      });
      await alert.present();
      return;
    }

    let obj = {
      type: 'submitts',
      userId: this.appMin.getUserid(),
      cause: this.cause,
      OtherCause: this.OtherCause,
      orderId: this.orderId
    }
    this.send(JSON.stringify(obj), true)

  }
  async dataHandle(meg: any) {
    if (meg['type'] == "addComplaintData_ret") {
      //加载初始数据
      console.log('addComplaintData_ret=' + JSON.stringify(meg));

      //this.toUserCName = meg['CName']
      this.txt = meg['txt'];
      this.date = meg['date'];
      this.isjubao = meg['isjubao'];
      this.ishowjb();
    }
    if (meg['type'] == 'submitts_ret') {
      console.log("submitts_ret=" + JSON.stringify(meg));
      //投诉成功

      let alert = await this.alertCtrl.create({
        header: '',
        subHeader: '投诉成功，我们将尽快给答复。',
        buttons: ['确定']
      });
      alert.present();
      //重新加载
      await this.addComplaintData();
    }
  }
  onRadio(txt) {
    console.log('onRadio=' + txt);
    this.cause = txt;
  }
  ishowjb(): boolean {
    if (this.isjubao == 'True') {
      //已经被举报了
   //   console.log('已经被举报了');
      return true;
    } else {
   //   console.log('没有被举报');
      return false;
    }
  }

  ngOnInit() {
    console.log("ngOnInit!")
    this.addComplaintData();//加载初始数据
  }

}
